.navbar-header img {
    height: 37px;
    width: 144px;
}

.navbar-brand {
    height: auto;
    padding: 0 15px;
}

.navbar {
    padding: 1.2rem 15px;
}

.navbar-right {
    margin-right: -15px;
}

.navbar-default {
    background-color: white;
}

.navbar-default .navbar-nav > li > a {
    color: $blue;
    font-size: 1.35rem;
    padding: 0 15px;
}

.navbar-default .navbar-nav > li > a > i {
    color: $blue !important;
}

.navbar-default .navbar-nav > li > a > i:hover {
    color: $red !important;
}

.navbar-default .navbar-nav > .active > a, .navbar-default .navbar-nav > .active > a:hover, .navbar-default .navbar-nav > .active > a:focus {
    color: $red;
    background-color: white;
    text-decoration: underline;
}

.navbar-default .navbar-nav > li > a:hover, .navbar-default .navbar-nav > li > a:focus {
    color: $red;
    background-color: transparent;
    text-decoration: underline;
}

.navbar-default .navbar-nav > li > a > i {
    color: $red;
    margin-right: 15px
}

.navbar-default .navbar-nav > li {
    height: 46px;
}

.navbar-default .navbar-nav > li > a {
    margin-top: 13px;
}

.navbar-default .navbar-nav > li > a.button {
    color: white;
    padding: 7px 40px !important;
    margin-top: 0;
}

.navbar-default .navbar-nav > li > a.button:hover {
    color: $red;
    text-decoration: none;
}

nav.navbar {
    @include transition;
}

nav.navbar.shrink {
    padding: 10px 15px;
    box-shadow: 0 2px 3px rgba(7, 7, 78, .6);
}

.search-dropdown {
    display: none;
    width: 100%;
    padding: 0 100px;
    background: rgba(255, 255, 255, .9);
    z-index: 999999999999 !important;
    position: fixed;
    top: 0 !important;
    border-top: 2px solid #d1d1d1;
    border-bottom: 2px solid #d1d1d1;
    & form {
        padding: 2rem 0;
    }
    & input[type="text"] {
        width: 100%;
        padding: 10px;
    }
}

.request-dropdown {
    display: none;
    width: 100%;
    padding: 0 100px;
    background: rgba(255, 255, 255, .9);
    z-index: 999999999999 !important;
    position: fixed;
    top: 0 !important;
    border-top: 2px solid #d1d1d1;
    border-bottom: 2px solid #d1d1d1;
    & form {
        padding: 0 0 2rem 0;
    }
    & input[type="text"] {
        width: 100%;
        padding: 10px;
        font-size: 28px;
        background-color: #f3f3f3;
        border: 1px solid #333;
        border-radius: 2px;
    }
    & input[type="email"] {
        width: 100%;
        padding: 10px;
        font-size: 28px;
        background-color: #f3f3f3;
        border: 1px solid #333;
        border-radius: 2px;
    }
    & [class*="col-"] {
        padding-left: 0;
    }
}
