.page-template-aboutus #heading {
}

.page-template-aboutus {
    h1 {
        margin: 0px;
        padding: 0px;
    }
    h2 {
        margin: 0px;
        padding-bottom: 50px;
    }
    & [class*="fa-"] {
        color: $grey-medium;
    }
    & .fa-linkedin-square:hover {
        color: #0077b5;
    }
    & .fa-facebook-square:hover {
        color: #3c5a99;
    }
    & .fa-twitter-square:hover {
        color: #1da1f2;
    }
    & .fa-google-plus-square:hover {
        color: #db3236;
    }
    & .fa-linkedin-square, & .fa-facebook-square, & .fa-twitter-square, & .fa-google-plus-square {
        transition: color .6s ease-out;
    }
    & .fa-linkedin-square:hover, & .fa-facebook-square:hover, & .fa-twitter-square:hover, & .fa-google-plus-square:hover {
        transform: translateY(-5px);
        text-shadow: 1px 1px rgba(7, 7, 78, .5);
    }
}

.page-template-aboutus {
    & #team-left {
        /*margin:-500px 0 200px 0;*/
        & ul li {
            display: inline;
            margin-right: 1rem;
        }
        & .profile {
            border: 5px solid $red;
            border-radius: 2px;
            //margin-left: 17%;
            padding: 5% 6%;
        }
        & .panel-default {
            border: none;
        }
        & .panel-default > .panel-heading + .panel-collapse > .panel-body {
            border-top-color: white !important;
            border-bottom-color: white;
        }
        & .panel-default > .panel-heading {
            background-color: white;
            border-color: white;
            height: 50px;
        }
        & .panel-heading, & .panel-body {
            padding: 0px;
        }
        & .panel-body > .container {
            padding: 0px;
            width: auto;
        }
        & .panel-heading .accordion-toggle:after {
            font-family: 'FontAwesome';
            content: "\f0d7";
            float: right;
            font-size: 2rem;
            color: $blue;
        }
        & .profile-image {
            width: 175px;
            height: 175px;
            top: 250px;
            /*
            margin-left: -650px;
            */
            margin-left: -38%;
            background-repeat: no-repeat;
            background-size: contain;
            position: relative;
            z-index: 2;
            left: 50%;
            border: 1px solid $blue;
            border-radius: 50%;
        }
        & .hexagon {
            width: 300px;
            height: 315px;
            top: 425px;
            /*
            margin-left: -780px;
            */
            margin-left: -46%;
            background-repeat: no-repeat;
            background-size: contain;
            position: relative;
            z-index: 2;
            left: 50%;
        }
    }
}

